<template>
  <div class="main" :id="`comment-${comment.id}`">
    <div class="comment-author">
      <img :src="`/img/avatars/${comment.author.avatar}`" />
      <p style="margin-left: 10px">
        <span class="comment-username" :class="comment.author.rank">{{
          comment.author.username
        }}</span>
        <br />
        <span class="comment-rank">{{ comment.author.rank }}</span>
      </p>

      <p class="comment-date">
        {{ new Date(comment.created_at) | moment("from", "now") }}
        <br>
        #{{comment.id}}
      </p>
    </div>

    <div v-if="comment.quotes.length" class="comment-replies">
      <li @click="scrollToPost(id)" v-for="id in comment.quotes" :key="id">{{ id }}#</li>
    </div>
    
    <div class="comment-text" v-html="comment.content"></div>

    <div v-if="comment.replies.length" class="comment-quotes">
      <li @click="scrollToPost(id)" v-for="id in comment.replies" :key="id">>>{{ id }}#</li>
    </div>

    <footer v-if="auth.authLevel >= 0" class="comment-options">
      <button>edit</button>
      <button>delete</button>
      <button v-if="!isQuoted" @click="quoteComment()">quote</button>
      <button v-else @click="unquoteComment()">unquote</button>
    </footer>
  </div>
</template>

<script>
export default {
  props: ["comment"],
  data() {
    return {};
  },
  methods: {
    quoteComment() {
      this.quoted = true;
      this.$store.commit("ADD_QUOTE", {
        quoteId: this.comment.id,
        body: this.comment,
      });
    },
    unquoteComment() {
      this.quoted = true;
      this.$store.commit("REMOVE_QUOTE", this.comment.id);
    },
    scrollToPost(id) {
      this.$scrollTo(`#comment-${id}`, 0, {
        offset: -60,
        force: true,
        cancelable: true,
      });
    },
  },
  computed: {
    auth() {
      return this.$store.getters["user/permissions"];
    },
    isQuoted() {
      return this.$store.getters["IS_QUOTED"](this.comment.id);
    },
  },
};
</script>

<style lang='scss'>
.comment {
  &-replies {
    margin: 5px;
    li {
      list-style-position: inside;
      margin: 2px;
      font-size: 12px;
      color: #335281;
    }
  }
  &-quotes {
    margin: 5px;
    li {
      list-style-position: inside;
      display: inline-block;
      margin: 2px 5px 2px 2px;
      font-size: 12px;
      color: #816233;
    }
  }
}
</style>